const Region = (countryCode) =>{
    const LetUp = countryCode.toUpperCase() 
    switch (LetUp) {
        case "SVK":
            return [{regionName:"Bratislava"},{regionName:"Trnava"},{regionName:"Nitra"},{regionName:"Trencin"},{regionName:"Zilina"},{regionName:"BanskaBystrica"},{regionName:"Presov"},{regionName:"Kosice"}]
            
        case "CZE":
            return [{regionName:"Prague"},{regionName:"CentralBohemian"},{regionName:"Liberec"},{regionName:"UstinadLabem"},{regionName:"KarlovyVary"},{regionName:"Plzen"},{regionName:"SouthBohemian"},{regionName:"Vysocina"},{regionName:"Pardubice"},{regionName:"HradecKralove"},{regionName:"SouthMoravian"},{regionName:"Olomouc"},{regionName:"Moravian-Silesian"},]
        
        case "UKR":
            return [{regionName:'Kyiv'},]
        default:
            return null;
            break;
    }
}
export default Region;